import React, { useEffect, useState } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import { useHistory } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import Swal from "sweetalert2";
import SVG from "react-inlinesvg";
import NoDataTable from "../../../common/noDataTable";
import Pagination from "@material-ui/lab/Pagination";
import {
  ApiDelete,
  ApiGet,
  ApiPost,
  ApiPut,
  ApiUpload,
  Bucket,
} from "../../../helpers/API/ApiData";
import { ErrorToast, SuccessToast } from "../../../helpers/Toast";
import { toAbsoluteUrl } from "../../../_metronic/_helpers";
import moment from "moment";
import { Dropdown, Form, Modal } from "react-bootstrap";
import Select from "react-select";
import { AiFillEye } from "react-icons/ai";
import { MdCancel } from 'react-icons/md'

export default function Discount() {
  const date = new Date();
  const history = useHistory();
  const [data, setData] = useState([]);
  const [modal, setModal] = useState(false);
  const [viewData, setViewData] = useState({});
  const [viewModal, setViewModal] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [totalpage, settotalpage] = useState(0);
  const [currentpage, setcurrentpage] = useState(1);
  const [pagesize, setpagesize] = useState(10);
  const [searching, setsearching] = useState("");
  const [image, setImage] = useState('')
  const [readMore, setReadMore] = useState(false);
  const [newData, setNewData] = useState({
    imageUrl: "",
    spicesName: "",
    category: "",
    description: "",
    status: true,
    id: ""
  });

  const columns = [
    {
      dataField: "_id",
      text: "ID",
      // sort: true,
      formatter: (cell, row, i) => {
        return <div className="d-flex align-items-center">{i + 1 || "-"}</div>;
      },
    },
    {
      dataField: "imageUrl",
      text: "Image",
      // sort: true,
      formatter: (cell, row) => {
        return (
          <div >
            <img src={cell} style={{ width: '100px', height: '100px', borderRadius: '10px' }} />
          </div>
        );
      },
    },
    {
      dataField: "spicesName",
      text: "Spices Name",
      sort: true,
      formatter: (cell, row) => {
        return (
          <div>
            {cell || "-"}
          </div>
        );
      },
    },
    {
      dataField: "description",
      text: "Description",
      sort: true,
      formatter: (cell, row) => {
        return <div>{cell || "-"}</div>;
      },
    },
    {
      dataField: "category",
      text: "Category",
      sort: true,
      formatter: (cell, row) => {
        return <div>{cell || "-"}</div>;
      },
    },
    {
      dataField: "date",
      text: "Date",
      sort: true,
      formatter: (cell, row) => {
        return <div>{moment(cell).format("DD-MM-YYYY") || "-"}</div>;
      },
    },
    {
      dataField: "status",
      text: "Status",
      sort: true,
      formatter: (cell, row) => {
        return <div>{cell === true ? "In Stock" : "Out of Stock" || "-"}</div>;
      },
    },
    // {
    //   dataField: "country",
    //   text: "Country",
    //   sort: true,
    //   formatter: (cell, row) => {
    //     return <div>{cell || "-"}</div>;
    //   },
    // },
    {
      dataField: "action",
      text: "Action",
      sort: true,
      formatter: (cell, row) => {
        return (
          <>
            {/* <a
              title="View User"
              className="btn btn-icon btn-light btn-hover-primary btn-sm me-3"
              onClick={() => viewBtnClick(row)}
            >
              <span className="svg-icon svg-icon-md svg-icon-primary eye-icon">
                <AiFillEye className="" />
              </span>
            </a> */}
            <a
              title="Edit customer"
              className="btn btn-icon btn-light btn-hover-primary btn-sm me-3"
              onClick={() => editBtnClick(row)}
            >
              <span className="svg-icon svg-icon-md svg-icon-primary">
                <SVG
                  src={toAbsoluteUrl(
                    "/media/svg/icons/Communication/Write.svg"
                  )}
                />
              </span>
            </a>

            <a
              title="Delete customer"
              className="btn btn-icon btn-light btn-hover-danger btn-sm"
              onClick={() => {
                Swal.fire({
                  text: `Are you sure you want to Delete ?`,
                  icon: "warning",
                  showCancelButton: true,
                  showConfirmButton: true,
                  confirmButtonText: `Yes, Delete`,
                  confirmButtonColor: "#D72852",
                  cancelButtonColor: "transparent",
                  cancelButtonText: "No, Cancel",
                }).then((res) => {
                  if (res.isConfirmed) {
                    onDelete(row?._id);
                  }
                });
              }}
            >
              <span className="svg-icon svg-icon-md svg-icon-danger">
                <SVG
                  src={toAbsoluteUrl("/media/svg/icons/General/Trash.svg")}
                />
              </span>
            </a>
          </>
        );
      },
    },
  ];

  const handleChange = (e) => {
    const { value, name } = e.target;
    setNewData({ ...newData, [name]: value });
  };
  const editBtnClick = (row) => {
    setIsEdit(true);
    setModal(true);
    setImage(row?.imageUrl)
    setNewData({
      id: row?._id,
      spicesName: row?.spicesName,
      description: row?.description,
      category: row?.category,
      status: row?.status
    });
  };
  const onDelete = async (Id) => {
    await ApiDelete(`/delete/${Id}`)
      .then(() => {
        Swal.fire({
          text: "You have Deleted Successfully!!!",
          icon: "success",
          confirmButtonText: "Ok, Got it!",
          confirmButtonColor: "#338DE6",
        });
        getService()
      })
      .catch((err) => ErrorToast(err?.message));
  };
  const onUpdate = async () => {
    let body = {
      imageUrl: image,
      spicesName: newData?.spicesName,
      category: newData?.category,
      description: newData?.description,
      status: newData?.status,
    }
    await ApiPut(`/updatespices/${newData?.id}`, body)
      .then((res) => {
        SuccessToast("Updated Successfully!");
        getService()
        setModal(false);
      })
      .catch((err) => ErrorToast(err?.message));
  };

  const handleSubmit = async () => {
    let body = {
      imageUrl: image,
      spicesName: newData?.spicesName,
      category: newData?.category,
      description: newData?.description,
      status: newData?.status,
    }
    if (newData?.spicesName && newData?.category && newData?.description) {
      await ApiPost("/createspices", body)
        .then((res) => {
          SuccessToast("SuccessFully Spice Created");
          setModal(false);
          setImage("")
          getService()
        })
        .catch((err) => ErrorToast(err?.error))

    } else {
      ErrorToast("please fill all the fields")
    }

  };
  const handleonchnagespagination = (e) => {
    setpagesize(e.target.value);
    setcurrentpage(1);
  };
  const onPaginationChange = (e, i) => {
    setcurrentpage(i);
  };

  const handlesearch = (e) => {
    setsearching(e.target.value);
  };

  const handleDelete = async () => {
    setImage("");
    await ApiPost("/deleteImage", { url: image }).then((res) => {
    }).catch((err) => {
    })
  }

  const imageChange = async (e) => {
    let formData = new FormData();
    formData.append("imageUrl", e.target.files[0]);
    await ApiUpload("/imageupload", formData)
      .then((res) => {
        setImage(res?.data?.url);
      })
  };

  const getService = async () => {
    await ApiGet(`/viewall?page=${currentpage}&search=${searching}`)
      .then((res) => {
        setData(res?.data?.spices);
        settotalpage(res?.data?.totalPages)
        setcurrentpage(res?.data?.currentPage)
        // setCountryData(res?.data?.data?.response);
      })
      .catch((err) => ErrorToast(err?.message));
  };
  useEffect(() => {
    getService();
  }, [currentpage, searching]);
  // useEffect(() => {
  //   fetchData(currentpage, pagesize, searching);
  // }, []);
  return (
    <>
      <div className="d-flex justify-content-between mb-4">
        <div className="title">
          <div className="fs-20px fw-bolder">Spice List</div>
          <div>
            <span
              role="button"
              onClick={() => history.push("/dashboard")}
              className="text-hover-info text-muted"
            >
              Home
            </span>{" "}
            -{" "}
            <span className="text-muted" role="button">
              {" "}
              Spice
            </span>
          </div>
        </div>
      </div>
      <div class="d-flex flex-column flex-column-fluid h-100" id="kt_content">
        <div class="card card-custom">
          <div class="card-header flex-wrap border-0 pt-6 pb-0 w-100">
            <div class="card-title ">
              {/* <h3 class="card-label">Video Playlist</h3> */}
              <div class="input-icon">
                <input
                  type="text"
                  class="form-control bg-light"
                  name="searchText"
                  placeholder="Search by Name"
                  value={searching}
                  onChange={(e) => handlesearch(e)}
                  id="kt_datatable_search_query"
                />
                <span>
                  <i class="flaticon2-search-1 text-muted"></i>
                </span>
              </div>
            </div>

            <div class="card-toolbar">
              <a
                class="btn btn-color font-weight-bolder btn-sm ms-4"
                onClick={() => {
                  setIsEdit(false);
                  setModal(true);
                  setImage("")
                  setNewData({
                    imageUrl: "",
                    spicesName: "",
                    category: "",
                    description: "",
                    status: true,
                  });
                }}
              >
                Add Spice
              </a>
            </div>
          </div>
          <div className="card-body mb-5">
            <BootstrapTable
              wrapperClasses="table-responsive"
              headerWrapperClasses="border-0"
              bordered={false}
              classes="table table-head-custom table-vertical-center overflow-hidden"
              bootstrap4
              keyField="_id"
              // selectRow={selectRow}
              data={data || []}
              columns={columns}
              // pagination={paginationFactory(options)}
              // defaultSorted={defaultSorted}
              noDataIndication={() => <NoDataTable />}
            // filter={filterFactory()}
            />
            <div class="d-flex justify-content-between  pt-10">
              <div className="my-2">
                <Pagination
                  count={totalpage}
                  page={currentpage}
                  onChange={onPaginationChange}
                  variant="outlined"
                  shape="rounded"
                  className="pagination_"
                />
              </div>
              {/* <div class="my-2">
                <div class="d-flex align-items-center pagination-drpdown">
                  <select
                    class="form-control pagination-drpdown1 dropdownPage"
                    id="kt_datatable_search_status"
                    onChange={(e) => handleonchnagespagination(e)}
                    value={pagesize}
                  >
                    <option value={10}>10</option>
                    <option value={20}>20</option>
                    <option value={30}>30</option>
                    <option value={50}>50</option>
                  </select>
                </div>
              </div> */}
            </div>
          </div>
        </div>
      </div>
      <Modal show={modal} onHide={() => setModal(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title>Add New Sevices</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <Form.Group className="col-md-12">
              <Form.Label>Spice Photo</Form.Label>
              <Form.Control
                type="file"
                placeholder=""
                label="driver_photo"
                id="image"
                required
                name="driver_photo"
                onChange={imageChange}
                value={""}
              />
              <span className="errorInput"></span>
            </Form.Group>
            {image && (
              <div className="position-relative w-auto">
                <img
                  src={image}
                  alt="sd"
                  style={{ width: "70px", height: "70px" }}
                  className="rounded-3"
                />
                <span
                  className="imageRemovoIconSpan"
                  onClick={() => {
                    handleDelete()
                  }}
                >
                  <MdCancel color="red" fontSize={20} />
                </span>
              </div>
            )}
            <Form.Group className="col-md-12">
              <Form.Label>Spices Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter Spices Name"
                label="spicesName"
                id="spicesName"
                required
                name="spicesName"
                onChange={handleChange}
                value={newData.spicesName}
              />
            </Form.Group>

            <Form.Group className="col-md-12">
              <Form.Label>Category Type</Form.Label>
              <Form.Control
                as="select"
                custom
                id="category"
                required
                className="form-control"
                name="category"
                onChange={handleChange}
                value={newData.category}
              >
                <option value="" disabled>
                  Select Category Type
                </option>

                <option value="Spices">Spices</option>
                <option value="OilSeeds">Oil Seeds</option>
                <option value="PowderSpices">Powder Spices</option>
                <option value="Dehydrated">Dehydrated</option>
                <option value="Fruits">Fruits</option>


              </Form.Control>
            </Form.Group>
            <Form.Group className="col-md-12">
              <Form.Label>Description</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter Description"
                label="name"
                id="description"
                required
                name="description"
                onChange={handleChange}
                value={newData.description}
              />
            </Form.Group>
            <Form.Group className="col-md-12">
              <Form.Label>Status:</Form.Label>
              <div>
                <Form.Check
                  inline
                  label="In Stock"
                  type="radio"
                  id="option1"
                  name="options"
                  onChange={() => setNewData({ ...newData, status: true })}
                  checked={newData.status}
                />
                <Form.Check
                  inline
                  label="Out of Stock"
                  type="radio"
                  id="option2"
                  name="options"
                  onChange={() => setNewData({ ...newData, status: false })}
                  checked={!newData.status}
                />
              </div>
            </Form.Group>
          </div>
        </Modal.Body>
        <Modal.Footer>
          {isEdit ? (
            <button className="btn btn-color" onClick={onUpdate}>
              Update Changes
            </button>
          ) : (
            <button className="btn btn-color" onClick={handleSubmit}>
              Submit
            </button>
          )}
        </Modal.Footer>
      </Modal>
      <Modal
        show={viewModal}
        onHide={() => {
          setViewModal(false);
          setReadMore(false);
        }}
        centered
        className="modal-width"
      >
        <Modal.Header closeButton>
          <Modal.Title>Service Details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <div className="text-center" style={{ marginBottom: "1.75rem" }}>
              <img
                src={
                  viewData?.image ||
                  "https://cdn.icon-icons.com/icons2/1378/PNG/512/avatardefault_92824.png"
                }
                alt=""
                className=" rounded-pill object-cover"
                height="100px"
                width="100px"
              />
            </div>
            {/* <div className="row py-2">
              <div className="col-md-5 fw-bold">Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.name}
              </div>
            </div> */}
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Email </div>
              <div className="col-md-7 text-capitalize">
                {viewData?.email}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Type</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.type}
              </div>
            </div>
            {viewData?.type === "Transportation" && <><div className="row py-2">
              <div className="col-md-5 fw-bold">Driver Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.data?.driver_name}
              </div>
            </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Driver Number</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.driver_no}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Licence Number</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.licenceNumber}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Rcbook</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.rcbook}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Vehicle Type</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.vehicleType}
                </div>
              </div></>}
            {viewData?.type === "Veterinary" && <><div className="row py-2">
              <div className="col-md-5 fw-bold">Doctor Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.data?.doctor_name}
              </div>
            </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Doctor Number</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.doctor_no}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Slot Time</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.slot}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Visitng Address</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.visitng_address}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Description</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.Doctor_description}
                </div>
              </div></>}
            {viewData?.type === "Insurance" && <><div className="row py-2">
              <div className="col-md-5 fw-bold">Company Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.data?.company_name}
              </div>
            </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Policy Description</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.policy_description}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Visitng Address</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.insaurance_address}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Contact Number</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.insaurance_no}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Website Link</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.company_link}
                </div>
              </div></>}
            {viewData?.type === "Accessories" && <>
              {/* <div className="row py-2">
              <div className="col-md-5 fw-bold">Doctor Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.data?.doctor_name}
              </div>
            </div> */}
              <div className="row py-2">
                <div className="col-md-5 fw-bold">price</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.accessory_price}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Accessories Description</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.accessory_description}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Website Link</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.accessory_link}
                </div>
              </div></>}
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button
            className="btn btn-color"
            onClick={() => {
              setViewModal(false);
              setReadMore(false);
            }}
          >
            Cancel
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
