import React, { useEffect, useState } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import { useHistory } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import Swal from "sweetalert2";
import SVG from "react-inlinesvg";
import NoDataTable from "../../../common/noDataTable";
import Pagination from "@material-ui/lab/Pagination";
import {
  ApiDelete,
  ApiGet,
  ApiPost,
  ApiPut,
  ApiUpload,
  Bucket,
} from "../../../helpers/API/ApiData";
import { ErrorToast, SuccessToast } from "../../../helpers/Toast";
import { toAbsoluteUrl } from "../../../_metronic/_helpers";
import moment from "moment";
import { Dropdown, Form, Modal } from "react-bootstrap";
import Select from "react-select";
import { AiFillEye } from "react-icons/ai";
import { MdCancel } from 'react-icons/md'

export default function Order() {
  const date = new Date();
  const history = useHistory();
  const [data, setData] = useState([]);
  const [modal, setModal] = useState(false);
  const [viewData, setViewData] = useState({});
  console.log("viewData[1]", viewData[1]);
  const [viewModal, setViewModal] = useState(false);
  const [isEdit, setIsEdit] = useState(false);
  const [totalpage, settotalpage] = useState(0);
  const [currentpage, setcurrentpage] = useState(1);
  const [pagesize, setpagesize] = useState(10);
  const [searching, setsearching] = useState("");
  const [image, setImage] = useState('')
  const [countryData, setCountryData] = useState([]);
  const [categoryData, setCategoryData] = useState([]);
  const [readMore, setReadMore] = useState(false);
  console.log("first", readMore);
  const [newData, setNewData] = useState({
    image: "",
    name: "",
    discount: "",
    expDate: "",
    categoryName: "",
    description: "",
    serviceType: "Transportation"
  });
  console.log('newData', newData)

  const columns = [
    {
      dataField: "_id",
      text: "ID",
      // sort: true,
      formatter: (cell, row, i) => {
        return <div className="d-flex align-items-center">{i + 1 || "-"}</div>;
      },
    },
    {
      dataField: "Name",
      text: "Name",
      // sort: true,
      formatter: (cell, row, i) => {
        return <div className="d-flex align-items-center">{row?.vendorId?.name || "-"}</div>;
      },
    }, {
      dataField: "Email",
      text: "Email",
      // sort: true,
      formatter: (cell, row, i) => {
        return <div className="d-flex align-items-center">{row?.vendorId?.email || "-"}</div>;
      },
    },
    {
      dataField: "Image",
      text: "Image",
      sort: true,
      formatter: (cell, row) => {
        return (
          <div>
            <img src={row?.productId?.frontPhoto} alt="" width={50} height={50} />
          </div>
        );
      },
    },
    {
      dataField: "Animal Name",
      text: "Animal Name",
      sort: true,
      formatter: (cell, row) => {
        return <div>{row?.productId?.animalType || "-"}</div>;
      },
    },
    {
      dataField: "Breed Type",
      text: "Breed Type",
      sort: true,
      formatter: (cell, row) => {
        return <div>{row?.productId?.breedType || "-"}</div>;
      },
    },
    {
      dataField: "Phone Number",
      text: "Phone Number",
      sort: true,
      formatter: (cell, row) => {
        return <div>{row?.productId?.phone || "-"}</div>;
      },
    }, {
      dataField: "Price",
      text: "Price",
      sort: true,
      formatter: (cell, row) => {
        return <div>₹{row?.productId?.price || "-"}</div>;
      },
    }, {
      dataField: "Age",
      text: "Age",
      sort: true,
      formatter: (cell, row) => {
        return <div>{row?.productId?.age || "-"}</div>;
      },
    },
    {
      dataField: "Age",
      text: "Date/Time",
      sort: true,
      formatter: (cell, row) => {
        return <div>{moment(row?.createdAt).format("LLL") || "-"}</div>;
      },
    },
    // {
    //   dataField: "country",
    //   text: "Country",
    //   sort: true,
    //   formatter: (cell, row) => {
    //     return <div>{cell || "-"}</div>;
    //   },
    // },
  ];

  const handleChange = (e) => {
    const { value, name } = e.target;
    setNewData({ ...newData, [name]: value });
  };
  console.log("newData", newData);
  const editBtnClick = (row) => {
    console.log("row", row);
    setIsEdit(true);
    setModal(true);
    setNewData({
      id: row?._id,
      image: row?.image,
      name: row?.name,
      discount: row?.discount,
      expDate: moment(row?.expDate).format("YYYY-MM-DD"),
      categoryName: row?.categoryName,
      description: row?.description,
    });
  };
  const onDelete = async (Id) => {
    await ApiDelete(`/service/delete-service?id=${Id}`)
      .then(() => {
        Swal.fire({
          text: "You have Deleted Successfully!!!",
          icon: "success",
          confirmButtonText: "Ok, Got it!",
          confirmButtonColor: "#338DE6",
        });
        getService()
      })
      .catch((err) => ErrorToast(err?.message));
  };
  const onUpdate = async () => {
    let body = {
      id: newData?.id,
      image: newData?.image,
      name: newData?.name,
      discount: newData?.discount,
      expDate: moment(newData?.expDate).format("YYYY-MM-DD"),
      categoryName: newData?.categoryName,
      description: newData?.description,
    };
    console.log("body", body);
    await ApiPut("/update/discount", body)
      .then((res) => {
        SuccessToast(res?.data?.message);
        setModal(false);
      })
      .catch((err) => ErrorToast(err?.message));
  };
  const viewBtnClick = (row) => {
    // setIsEdit(true);
    setViewModal(true);
    setViewData(row);
    // setNewData({
    //   id: row?._id,
    //   name: row?.eventTypeName,
    //   // date: moment(row?.date).format("YYYY-MM-DD"),
    //   // country: row?.country,
    // });
  };
  const handleSubmit = async () => {
    let body = {}
    if (newData?.serviceType === "Transportation") {
      body = {
        email: newData?.email,
        // driver_name: newData?.driver_name,   
        type: newData?.serviceType,
        image: image,
        data: {
          driver_name: newData?.driver_name,
          driver_no: newData?.driver_no,
          vehicleType: newData?.vehicleType ?? "Truck",
          licenceNumber: newData?.licenceNumber,
          rcbook: newData?.rcbook,
          driverDescription: newData?.driverDescription,
        }
      };
    }
    else if (newData?.serviceType === "Veterinary") {
      body = {
        email: newData?.email,
        type: newData?.serviceType,
        image: image,
        data: {
          doctor_name: newData?.doctor_name,
          doctor_no: newData?.doctor_no,
          slot: newData?.slot,
          licenceNumber: newData?.licenceNumber,
          visitng_address: newData?.visitng_address,
          Doctor_description: newData?.Doctor_description,
          doctor_degree: newData?.doctor_degree,
        }
      };
    }
    else if (newData?.serviceType === "Insurance") {
      body = {
        email: newData?.email,
        type: newData?.serviceType,
        image: image,
        data: {
          company_name: newData?.company_name,
          policy_description: newData?.policy_description,
          insaurance_address: newData?.insaurance_address,
          insaurance_no: newData?.insaurance_no,
          company_link: newData?.company_link,
        }
      };
    } else if (newData?.serviceType === "Accessories") {
      body = {
        email: newData?.email,
        type: newData?.serviceType,
        image: image,
        data: {
          accessory_name: newData?.accessory_name,
          accessory_price: newData?.accessory_price,
          accessory_description: newData?.accessory_description,
          accessory_link: newData?.accessory_link,
        }
      };
    }
    console.log("body", body)
    if (newData?.serviceType === "Transportation") {
      if (newData?.email && newData?.serviceType && image && newData?.driver_name && newData?.driver_no && newData?.licenceNumber && newData?.rcbook && newData?.driverDescription) {
        await ApiPost("/service/add-service", body)
          .then((res) => {
            SuccessToast(res?.data?.message);
            setModal(false);
            setImage("")
            getService()
          })
          .catch((err) => ErrorToast(err?.message))

      } else {
        ErrorToast("please fill all the fields")
      }
    } else if (newData?.serviceType === "Veterinary") {
      if (newData?.email && newData?.serviceType && image && newData?.doctor_name && newData?.doctor_no && newData?.slot && newData?.licenceNumber && newData?.visitng_address && newData?.Doctor_description && newData?.doctor_degree) {
        await ApiPost("/service/add-service", body)
          .then((res) => {
            SuccessToast(res?.data?.message);
            setModal(false);
            setImage("")
            getService()
          })
          .catch((err) => ErrorToast(err?.message));
      } else {
        ErrorToast("please fill all the fields")

      }
    } else if (newData?.serviceType === "Insurance") {
      if (newData?.email && newData?.serviceType && image && newData?.company_name && newData?.policy_description && newData?.insaurance_address && newData?.insaurance_no && newData?.company_link) {
        await ApiPost("/service/add-service", body)
          .then((res) => {
            SuccessToast(res?.data?.message);
            setModal(false);
            setImage("")
            getService()
          })
          .catch((err) => ErrorToast(err?.message))
      } else {
        ErrorToast("please fill all the fields")

      }
    } else if (newData?.serviceType === "Accessories") {
      if (newData?.email && newData?.serviceType && image && newData?.accessory_name && newData?.accessory_price && newData?.accessory_description && newData?.accessory_link) {
        await ApiPost("/service/add-service", body)
          .then((res) => {
            SuccessToast(res?.data?.message);
            setModal(false);
            setImage("")
            getService()
          })
          .catch((err) => ErrorToast(err?.message));
      } else {
        ErrorToast("please fill all the fields")

      }
    }

  };
  const handleonchnagespagination = (e) => {
    setpagesize(e.target.value);
    setcurrentpage(1);
  };
  const onPaginationChange = (e, i) => {
    setcurrentpage(i);
  };
  // const imageChange = async (e) => {
  //   let file = e.target.files[0];
  //   let fileURL = URL.createObjectURL(file);
  //   file.fileURL = fileURL;
  //   let formData = new FormData();
  //   formData.append("image", file);
  //   await ApiUpload("upload/image/discount", formData)
  //     .then((res) => {
  //       setNewData({ ...newData, image: res?.data?.data?.image });
  //     })
  //     .catch((err) => console.log("res_blob", err));
  // };
  // const handlesearch = (e) => {
  //   setsearching(e.target.value);
  // };
  const imageChange = async (e) => {
    let formData = new FormData();
    formData.append("image", e.target.files[0]);
    await ApiUpload("/image/upload-image", formData)
      .then((res) => {
        setImage(res?.data?.url);
      })
      .catch((err) => console.log("res_blob", err));
  };
  const getService = async () => {
    await ApiGet("/product/get-order")
      .then((res) => {
        console.log("res?.data?.data?.response", res?.data?.data);
        setData(res?.data?.getOrder);
        // setCountryData(res?.data?.data?.response);
      })
      .catch((err) => ErrorToast(err?.message));
  };
  useEffect(() => {
    getService();
  }, []);
  // useEffect(() => {
  //   fetchData(currentpage, pagesize, searching);
  // }, []);
  return (
    <>
      <div className="d-flex justify-content-between mb-4">
        <div className="title">
          <div className="fs-20px fw-bolder">Order List</div>
          <div>
            <span
              role="button"
              onClick={() => history.push("/dashboard")}
              className="text-hover-info text-muted"
            >
              Home
            </span>{" "}
            -{" "}
            <span className="text-muted" role="button">
              {" "}
              Order
            </span>
          </div>
        </div>
      </div>
      <div class="d-flex flex-column flex-column-fluid h-100" id="kt_content">
        <div class="card card-custom">
          <div class="card-header flex-wrap border-0 pt-6 pb-0 w-100">
            <div class="card-title ">
              {/* <h3 class="card-label">Video Playlist</h3> */}
              <div class="input-icon">
                <input
                  type="text"
                  class="form-control bg-light"
                  name="searchText"
                  placeholder="Search by Name"
                  value={searching}
                  // onChange={(e) => handlesearch(e)}
                  id="kt_datatable_search_query"
                />
                <span>
                  <i class="flaticon2-search-1 text-muted"></i>
                </span>
              </div>
            </div>

            <div class="card-toolbar">
              {/* <a
                class="btn btn-color font-weight-bolder btn-sm ms-4"
                onClick={() => {
                  setIsEdit(false);
                  setModal(true);
                  setNewData({
                    image: "",
                    name: "",
                    discount: "",
                    expDate: "",
                    categoryName: "",
                    description: "",
                    serviceType: "Transportation"
                  });
                }}
              >
                Add 
              </a> */}
            </div>
          </div>
          <div className="card-body mb-5">
            <BootstrapTable
              wrapperClasses="table-responsive"
              headerWrapperClasses="border-0"
              bordered={false}
              classes="table table-head-custom table-vertical-center overflow-hidden"
              bootstrap4
              keyField="_id"
              // selectRow={selectRow}
              data={data || []}
              columns={columns}
              // pagination={paginationFactory(options)}
              // defaultSorted={defaultSorted}
              noDataIndication={() => <NoDataTable />}
            // filter={filterFactory()}
            />
            <div class="d-flex justify-content-between  pt-10">
              <div className="my-2">
                <Pagination
                  count={totalpage}
                  page={currentpage}
                  onChange={onPaginationChange}
                  variant="outlined"
                  shape="rounded"
                  className="pagination_"
                />
              </div>
              <div class="my-2">
                <div class="d-flex align-items-center pagination-drpdown">
                  <select
                    class="form-control pagination-drpdown1 dropdownPage"
                    id="kt_datatable_search_status"
                    onChange={(e) => handleonchnagespagination(e)}
                    value={pagesize}
                  >
                    <option value={10}>10</option>
                    <option value={20}>20</option>
                    <option value={30}>30</option>
                    <option value={50}>50</option>
                  </select>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <Modal show={modal} onHide={() => setModal(false)} centered>
        <Modal.Header closeButton>
          <Modal.Title>Add New Sevices</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <Form.Group className="col-md-12">
              <Form.Label>E-mail </Form.Label>
              <Form.Control
                type="email"
                placeholder="Enter Email"
                label="email"
                id="email"
                required
                name="email"
                onChange={handleChange}
                value={newData.email}
              />
              <span className="errorInput">
                {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
              </span>
            </Form.Group>

            <Form.Group className="col-md-12">
              <Form.Label>Services Type</Form.Label>
              <Form.Control
                as="select"
                custom
                id="serviceType"
                required
                className="form-control"
                name="serviceType"
                onChange={handleChange}
                value={newData.serviceType}
              >
                <option value="" disabled>
                  Select Service Type
                </option>

                <option value="Transportation">Transportation</option>
                <option value="Veterinary">Veterinary</option>
                <option value="Insurance">Insurance</option>
                <option value="Accessories">Accessories</option>


              </Form.Control>

              {/* <span className="errorInput">
                {data.playlist?.length > 0 ? "" : errors["playlist"]}
              </span> */}
            </Form.Group>

            {newData.serviceType === "Transportation" && <><Form.Group className="col-md-12">
              <Form.Label>Driver Photo </Form.Label>
              <Form.Control
                type="file"
                placeholder=""
                label="driver_photo"
                id="image"
                required
                name="driver_photo"
                onChange={imageChange}
                value={""}
              />
              <span className="errorInput"></span>
            </Form.Group>
              {image && (
                <div className="position-relative w-auto">
                  <img
                    src={image}
                    alt="sd"
                    style={{ width: "70px", height: "70px" }}
                    className="rounded-3"
                  />
                  <span
                    className="imageRemovoIconSpan"
                    onClick={() => {
                      // setNewData({ ...newData, image: "" });
                      setImage("");
                      // document.getElementById("image").value = null;
                    }}
                  >
                    <MdCancel color="red" fontSize={20} />
                  </span>
                </div>
              )}
              <Form.Group className="col-md-12">
                <Form.Label>Driver Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Driver Name"
                  label="name"
                  id="name"
                  required
                  name="driver_name"
                  onChange={handleChange}
                  value={newData.driver_name}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Driver Contact NUmber</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Enter Driver Contact Number"
                  label="name"
                  id="name"
                  required
                  name="driver_no"
                  onChange={handleChange}
                  value={newData.driver_no}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>


              <Form.Group className="col-md-12">
                <Form.Label>Vehicle Type </Form.Label>
                <Form.Control
                  as="select"
                  custom
                  id="serviceType"
                  required
                  className="form-control"
                  name="vehicleType"
                  onChange={handleChange}
                  value={newData.vehicleType}
                >
                  <option value="" disabled>
                    Select Vehicle Type
                  </option>

                  <option value="Truck">Truck</option>
                  <option value="Trailers">Trailers</option>
                  <option value="Pickup">Pickup</option>
                </Form.Control>

                {/* <span className="errorInput">
                {data.playlist?.length > 0 ? "" : errors["playlist"]}
              </span> */}
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Licence Number</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Licence Number"
                  label="name"
                  id="name"
                  required
                  name="licenceNumber"
                  onChange={handleChange}
                  value={newData.licenceNumber}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>


              <Form.Group className="col-md-12">
                <Form.Label>RC Book Number</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter RC Book Number"
                  label="name"
                  id="name"
                  required
                  name="rcbook"
                  onChange={handleChange}
                  value={newData.rcbook}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Driver Description</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Driver Description"
                  label="date"
                  id="description"
                  required
                  name="driverDescription"
                  role="expDate"
                  onChange={handleChange}
                  value={newData.driverDescription}
                />
                <span className="errorInput"></span>
              </Form.Group></>}



            {newData.serviceType === "Veterinary" && <><Form.Group className="col-md-12">
              <Form.Label>Veterinary Doctor Photo </Form.Label>
              <Form.Control
                type="file"
                placeholder=""
                label="doctor photo"
                id="image"
                required
                name="driver_photo"
                onChange={imageChange}
                value={""}
              />
              <span className="errorInput"></span>
            </Form.Group>
              {image && (
                <div className="position-relative w-auto">
                  <img
                    src={image}
                    alt="sd"
                    style={{ width: "70px", height: "70px" }}
                    className="rounded-3"
                  />
                  <span
                    className="imageRemovoIconSpan"
                    onClick={() => {
                      // setNewData({ ...newData, image: "" });
                      setImage("");
                      // document.getElementById("image").value = null;
                    }}
                  >
                    <MdCancel color="red" fontSize={20} />
                  </span>
                </div>
              )}
              <Form.Group className="col-md-12">
                <Form.Label>Veterinary Doctor Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Doctor Name"
                  label="name"
                  id="name"
                  required
                  name="doctor_name"
                  onChange={handleChange}
                  value={newData.doctor_name}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>
              <Form.Group className="col-md-12">
                <Form.Label> Doctor Degree</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Doctor Name"
                  label="name"
                  id="name"
                  required
                  name="doctor_degree"
                  onChange={handleChange}
                  value={newData.doctor_degree}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>
              <Form.Group className="col-md-12">
                <Form.Label>Veterinary Doctor Contact Number</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Enter Driver Contact Number"
                  label="name"
                  id="name"
                  required
                  name="doctor_no"
                  onChange={handleChange}
                  value={newData.doctor_no}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Slot Timing</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Slot Timing(in local time)"
                  label="name"
                  id="name"
                  required
                  name="slot"
                  onChange={handleChange}
                  value={newData.slot}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>


              <Form.Group className="col-md-12">
                <Form.Label>Visitng Address</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Veterinary Doctor Description"
                  label="date"
                  id="description"
                  required
                  name="visitng_address"
                  role="expDate"
                  onChange={handleChange}
                  value={newData.visitng_address}
                />
                <span className="errorInput"></span>
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Veterinary Doctor Description</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Veterinary Doctor Description"
                  label="date"
                  id="description"
                  required
                  name="Doctor_description"
                  role="expDate"
                  onChange={handleChange}
                  value={newData.Doctor_description}
                />
                <span className="errorInput"></span>
              </Form.Group></>}







            {newData.serviceType === "Insurance" && <><Form.Group className="col-md-12">
              <Form.Label>Insurance Policy Photo </Form.Label>
              <Form.Control
                type="file"
                placeholder="Enter Insurance Policy Photo"
                label="doctor photo"
                id="image"
                required
                name="driver_photo"
                onChange={imageChange}
                value={""}
              />
              <span className="errorInput"></span>
            </Form.Group>
              {image && (
                <div className="position-relative w-auto">
                  <img
                    src={image}
                    alt="sd"
                    style={{ width: "70px", height: "70px" }}
                    className="rounded-3"
                  />
                  <span
                    className="imageRemovoIconSpan"
                    onClick={() => {
                      // setNewData({ ...newData, image: "" });
                      setImage("");
                      // document.getElementById("image").value = null;
                    }}
                  >
                    <MdCancel color="red" fontSize={20} />
                  </span>
                </div>
              )}
              <Form.Group className="col-md-12">
                <Form.Label>Insurance Company Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Insurance Company Name"
                  label="name"
                  id="name"
                  required
                  name="company_name"
                  onChange={handleChange}
                  value={newData.company_name}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>
              <Form.Group className="col-md-12">
                <Form.Label>Insurance Policy Description</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Insurance Policy Description"
                  label="date"
                  id="description"
                  required
                  name="policy_description"
                  role="expDate"
                  onChange={handleChange}
                  value={newData.policy_description}
                />
                <span className="errorInput"></span>
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Visitng Address</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Visitng Address of Insaurance Company"
                  label="date"
                  id="description"
                  required
                  name="insaurance_address"
                  role="expDate"
                  onChange={handleChange}
                  value={newData.insaurance_address}
                />
                <span className="errorInput"></span>
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Insaurance Company's Contact Number</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Enter Insaurance Company's Contact Number"
                  label="name"
                  id="name"
                  required
                  name="insaurance_no"
                  onChange={handleChange}
                  value={newData.insaurance_no}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Website Link of Insaurance Company</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Website Link of Insaurance Company"
                  label="date"
                  id="description"
                  required
                  name="company_link"
                  role="expDate"
                  onChange={handleChange}
                  value={newData.company_link}
                />
                <span className="errorInput"></span>
              </Form.Group></>}




            {newData.serviceType === "Accessories" && <><Form.Group className="col-md-12">
              <Form.Label>Animal Accessories Photo </Form.Label>
              <Form.Control
                type="file"
                placeholder="Animal Accessories Photo"
                label="doctor photo"
                id="image"
                required
                name="driver_photo"
                onChange={imageChange}
                value={""}
              />
              <span className="errorInput"></span>
            </Form.Group>
              {image && (
                <div className="position-relative w-auto">
                  <img
                    src={image}
                    alt="sd"
                    style={{ width: "70px", height: "70px" }}
                    className="rounded-3"
                  />
                  <span
                    className="imageRemovoIconSpan"
                    onClick={() => {
                      // setNewData({ ...newData, image: "" });
                      setImage("");
                      // document.getElementById("image").value = null;
                    }}
                  >
                    <MdCancel color="red" fontSize={20} />
                  </span>
                </div>
              )}
              <Form.Group className="col-md-12">
                <Form.Label>Animal Accessories Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Animal Accessories Name"
                  label="name"
                  id="name"
                  required
                  name="accessory_name"
                  onChange={handleChange}
                  value={newData.accessory_name}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>


              <Form.Group className="col-md-12">
                <Form.Label>Price</Form.Label>
                <Form.Control
                  type="number"
                  placeholder="Price"
                  label="name"
                  id="name"
                  required
                  name="accessory_price"
                  onChange={handleChange}
                  value={newData.accessory_price}
                />
                <span className="errorInput">
                  {/* {newData.name?.length > 0 ? "" : errors["name"]} */}
                </span>
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Animal Accessories Description</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter accessories description"
                  label="date"
                  id="description"
                  required
                  name="accessory_description"
                  role="expDate"
                  onChange={handleChange}
                  value={newData.accessory_description}
                />
                <span className="errorInput"></span>
              </Form.Group>

              <Form.Group className="col-md-12">
                <Form.Label>Website Link of Animal Accessories Company</Form.Label>
                <Form.Control
                  type="text"
                  placeholder="Enter Website Link of Animal Accessories Company"
                  label="date"
                  id="description"
                  required
                  name="accessory_link"
                  role="expDate"
                  onChange={handleChange}
                  value={newData.accessory_link}
                />
                <span className="errorInput"></span>
              </Form.Group></>}



          </div>
        </Modal.Body>
        <Modal.Footer>
          {isEdit ? (
            <button className="btn btn-color" onClick={onUpdate}>
              Update Changes
            </button>
          ) : (
            <button className="btn btn-color" onClick={handleSubmit}>
              Submit
            </button>
          )}
        </Modal.Footer>
      </Modal>
      <Modal
        show={viewModal}
        onHide={() => {
          setViewModal(false);
          setReadMore(false);
        }}
        centered
        className="modal-width"
      >
        <Modal.Header closeButton>
          <Modal.Title>Service Details</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="row">
            <div className="text-center" style={{ marginBottom: "1.75rem" }}>
              <img
                src={
                  viewData?.image ||
                  "https://cdn.icon-icons.com/icons2/1378/PNG/512/avatardefault_92824.png"
                }
                alt=""
                className=" rounded-pill object-cover"
                height="100px"
                width="100px"
              />
            </div>
            {/* <div className="row py-2">
              <div className="col-md-5 fw-bold">Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.name}
              </div>
            </div> */}
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Email </div>
              <div className="col-md-7 text-capitalize">
                {viewData?.email}
              </div>
            </div>
            <div className="row py-2">
              <div className="col-md-5 fw-bold">Type</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.type}
              </div>
            </div>
            {viewData?.type === "Transportation" && <><div className="row py-2">
              <div className="col-md-5 fw-bold">Driver Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.data?.driver_name}
              </div>
            </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Driver Number</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.driver_no}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Licence Number</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.licenceNumber}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Rcbook</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.rcbook}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Vehicle Type</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.vehicleType}
                </div>
              </div></>}
            {viewData?.type === "Veterinary" && <><div className="row py-2">
              <div className="col-md-5 fw-bold">Doctor Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.data?.doctor_name}
              </div>
            </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Doctor Number</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.doctor_no}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Slot Time</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.slot}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Visitng Address</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.visitng_address}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Description</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.Doctor_description}
                </div>
              </div></>}
            {viewData?.type === "Insurance" && <>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Company Name</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.company_name}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Policy Description</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.policy_description}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Visitng Address</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.insaurance_address}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Contact Number</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.insaurance_no}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Website Link</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.company_link}
                </div>
              </div></>}
            {viewData?.type === "Accessories" && <>
              {/* <div className="row py-2">
              <div className="col-md-5 fw-bold">Doctor Name</div>
              <div className="col-md-7 text-capitalize">
                {viewData?.data?.doctor_name}
              </div>
            </div> */}
              <div className="row py-2">
                <div className="col-md-5 fw-bold">price</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.accessory_price}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Accessories Description</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.accessory_description}
                </div>
              </div>
              <div className="row py-2">
                <div className="col-md-5 fw-bold">Website Link</div>
                <div className="col-md-7 text-capitalize">
                  {viewData?.data?.accessory_link}
                </div>
              </div></>}
          </div>
        </Modal.Body>
        <Modal.Footer>
          <button
            className="btn btn-color"
            onClick={() => {
              setViewModal(false);
              setReadMore(false);
            }}
          >
            Cancel
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
